    var SESSION_KEY = 'denglutai'
	import uniIcon from "@/components/uni-icon/uni-icon.vue"
	export default {
		data() {
			return {
			   username:''
			}
		},
		components:{
			uniIcon
		},
		methods: {
			closeBanner: function() {
				this.$emit('close')
			},
			saveUserName(){
				uni.setStorageSync('CNode-username', this.username.replace(/\s+/g,""));
